import { useEffect, useState } from "react";
import { getTransferHistory } from "./HistoryAPI";
import moment from "moment";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import $ from "jquery";
import Loading from "../common/Loading";
import ErrorMessage from "../common/ErrorMessage";
import { useArrayFetch } from "../common/DataFetchHook";
import Profile from "../profile/Profile";

function VPTransferHistory(props) {
  const fetch = (dateString) => getTransferHistory(dateString);
  const [date, setDate] = useState(moment().format("DD-MMM-YYYY"));
  const [data, loading, error] = useArrayFetch(date, fetch);

  useEffect(() => {
    document.title = "VStore | VP Transfer History";
    let datePicker = $("#datepicker").datepicker({
      uiLibrary: "bootstrap4",
      format: "dd-mmm-yyyy",
      maxDate: new Date(),
      change: handleChange,
    });
    return () => {
      datePicker && datePicker.destroy();
    };
  }, []);

  function handleChange({ target }) {
    setDate(target.value);
  }

  function getIcon(dir) {
    if (dir === "SEND") {
      return <FontAwesomeIcon icon={["fas", "minus-circle"]} size="lg" />;
    }
    return <FontAwesomeIcon icon={["fas", "plus-circle"]} size="lg" />;
  }

  function getNote(notes) {
    if (notes) {
      return (
        <>
          <label className="mt-2 mb-0 font-weight-medium">Notes</label>
          <div className="small text-muted">{notes}</div>
        </>
      );
    }
    return <></>;
  }

  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block order-2">
        <Profile />
      </div>
      <div className="col-md-12 col-lg-8 order-1">
        <div className="row">
          <div className="col-md-12">
            <h5>VP Transfer History</h5>
            <hr />
            <form className="form-row mb-3">
              <div className="col-auto">
                <input
                  id="datepicker"
                  type="text"
                  className="form-control bg-white"
                  defaultValue={date}
                  readOnly
                />
              </div>
            </form>

            <Loading show={loading} />
            <ErrorMessage message={error} />

            {!loading && !error && data && !data.length && (
              <div className="text-center text-muted">No data.</div>
            )}

            {!loading &&
              !error &&
              data &&
              data.map((h, index) => {
                let color =
                  h.direction === "SEND" ? " text-danger" : " text-success";
                return (
                  <div
                    key={index}
                    className="card border-0 rounded-lg shadow-cm mb-3 col-md-12"
                  >
                    <div className="card-body px-0">
                      <div className="media">
                        <span className={"mr-3" + color}>
                          {getIcon(h.direction)}
                        </span>
                        <div className="media-body">
                          <div className="d-flex justify-content-between">
                            <label
                              className={"my-0 font-weight-medium" + color}
                            >
                              VP {h.amount.toLocaleString("en")}
                            </label>
                            <label
                              className={"my-0 font-weight-medium text-muted"}
                            >
                              {h.direction}
                            </label>
                          </div>
                          <div className="mb-2 small text-muted">
                            {moment(h.txnDate).format("DD-MMM-yyyy hh:mm:ss A")}
                          </div>
                          <label className="my-0 font-weight-medium">
                            {h.direction === "SEND" ? "TO" : "FROM"}
                          </label>
                          <div className="small text-muted">
                            {h.partnerMobile}
                          </div>
                          {getNote(h.notes)}
                        </div>
                      </div>
                    </div>
                  </div>
                );
              })}
          </div>
        </div>
      </div>
    </>
  );
}

export default VPTransferHistory;
