import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";
import { useApiFetch } from "../../common/DataFetchHook";
import TextInput from "../../common/TextInput";
import Profile from "../Profile";
import MpuLogo from "./ic_mpu.png";
import { requestMpuPayment } from "./MpuPaymentAPI";

function MpuPayment(props) {
  const [point, setPoint] = useState({
    value: "",
    txnFee: 0,
    total: 0,
    confirm: false,
  });
  const [inputError, setInputError] = useState("");
  const [data, loading, error, dispatch] = useApiFetch((action) =>
    requestMpuPayment(action.body, action.token)
  );

  useEffect(() => {
    document.title = "VStore | MPU";
    if (data) {
      window.open(data.paymentUrl);
      props.history.replace("/deposit-options");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  useEffect(() => {
    if (error) {
      toast.error(error);
    }
  }, [error]);

  function handleChange({ target }) {
    const value = target.value;
    if (isNaN(value)) {
      return;
    }

    setPoint({
      ...point,
      value: value,
    });
  }

  function handleSubmit(event) {
    event.preventDefault();
    setInputError("");
    let value = parseFloat(point.value);

    if (value < 1000 || value > 1_000_000 || value % 100 !== 0) {
      setInputError(
        `Minimum 1,000 VP Points to maximum 1,000,000 VP Points. 
        The amount of VP Points must be divisible by 100.`
      );
      return;
    }

    let txnFee = value * 0.01;
    let total = value + txnFee;

    setPoint({
      value: point.value,
      txnFee: txnFee,
      total: total,
      confirm: true,
    });
  }

  function makePayment(event) {
    event.preventDefault();
    let body = { vpPoints: point.value };
    dispatch({ body: body });
  }

  function goBack(event) {
    event.preventDefault();
    setPoint({
      value: point.value,
      txnFee: 0,
      total: 0,
      confirm: false,
    });
  }

  let component = (
    <div className="card border-0 rounded-lg shadow-cm">
      <div className="card-body">
        <div className="row ">
          <img
            src={MpuLogo}
            alt=""
            width="68"
            height="68"
            className="mx-auto"
          />
        </div>
        <label className="font-weight-medium text-center d-block mb-3">
          Deposit VP Points using MPU card
        </label>
        <p className="small bg-light rounded px-3 py-2 border">
          Enter the amount of VP that you want to deposit. VStore will charge 1%
          of the amount of VP you are going to deposit as service fee. You can
          deposit minimum 1,000 VP Points to maximum 1,000,000 VP Points. The
          amount of VP Point must be divisible by 100.
        </p>
        <form onSubmit={handleSubmit}>
          <TextInput
            label="VP Points"
            name="points"
            type="number"
            placeholder="VP Points you want to deposit"
            value={point.value}
            onChange={handleChange}
            error={inputError}
          />
          <div className="clearfix">
            <button type="submit" className="btn btn-success float-right">
              Continue
            </button>
          </div>
        </form>
      </div>
    </div>
  );

  let lastBreadcrumb = <li className="breadcrumb-item active">MPU</li>;

  if (point.confirm) {
    lastBreadcrumb = (
      <li className="breadcrumb-item">
        <a href="!#" className="card-link" onClick={goBack}>
          MPU
        </a>
      </li>
    );
    component = (
      <div className="card border-0 rounded-lg shadow-cm">
        <div className="card-body">
          <div className="row ">
            <img src={MpuLogo} alt="" width="68" className="mx-auto" />
          </div>
          <label className="font-weight-medium text-center d-block mb-3">
            Deposit VP Points using MPU card
          </label>
          <p className="small bg-light rounded px-3 py-2 border mb-4">
            After successful payment, your account will receive VP Points
            immediately.
          </p>
          <div className="row mb-3">
            <div className="col">
              <h6>VP Points</h6>
            </div>
            <div className="col-auto">
              <h6 className="text-muted">
                VP {parseFloat(point.value).toLocaleString("en")}
              </h6>
            </div>
          </div>
          <div className="row">
            <div className="col">
              <h6>Transaction fee (MMK)</h6>
            </div>
            <div className="col-auto">
              <h6 className="text-muted">
                MMK {point.txnFee.toLocaleString("en")}
              </h6>
            </div>
          </div>
          <div className="dropdown-divider pb-2"></div>
          <div className="row mb-3">
            <div className="col">
              <h6>Total amount (MMK)</h6>
            </div>
            <div className="col-auto">
              <h6 className="text-success">
                MMK {point.total.toLocaleString("en")}
              </h6>
            </div>
          </div>
          <div className="clearfix">
            <button
              className="btn btn-success float-right"
              onClick={makePayment}
              disabled={loading}
            >
              {loading && (
                <>
                  <span
                    className="spinner-border spinner-border-sm"
                    role="status"
                    aria-hidden="true"
                  ></span>
                  &nbsp;
                </>
              )}
              Make payment
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block order-2">
        <Profile />
      </div>
      <div className="col-md-12 col-lg-8 order-1">
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb bg-white shadow-cm">
            <li className="breadcrumb-item">
              <Link to="/deposit-options" className="card-link">
                Deposit VP Point
              </Link>
            </li>
            {lastBreadcrumb}
          </ol>
        </nav>
        {component}
      </div>
    </>
  );
}

export default MpuPayment;
