import LabelGroup from "./LabelGroup";
import moment from "moment";
import { useEffect, useRef, useState } from "react";
import { useApiFetch } from "../common/DataFetchHook";
import { checkEpinStatus } from "../service/ProductAPI";
import { toast } from "react-toastify";

function PurchaseEPinInfo(props) {
  const [block, setBlock] = useState(0);
  const [status, setStatus] = useState(props.info.status);
  const [serialKey, setSerialKey] = useState(props.info.serialKey);
  const [expiryDate, setExpiryDate] = useState(props.info.expiryDate);
  const [data, loading, error, dispatch] = useApiFetch((action) =>
    checkEpinStatus(action.body, action.token)
  );
  const timer = useRef(null);
  const serialKeyRef = useRef(null);

  useEffect(() => {
    if (data) {
      setStatus(data.purchaseStatus);
      setSerialKey(data.serialKey);
      setExpiryDate(data.expiryDate);

      if (data.purchaseStatus === "PROCESSING") {
        setBlock(15);
      }
    }
  }, [data]);

  useEffect(() => {
    if (error) {
      toast.error(error);
    }
  }, [error]);

  useEffect(() => {
    if (block > 0) {
      timer.current = setTimeout(() => {
        let _block = block - 1;
        setBlock(_block);
      }, 1000);
    }
    return () => {
      timer.current && clearTimeout(timer.current);
      timer.current = null;
    };
  }, [block]);

  function checkStatus(event) {
    event.preventDefault();

    let body = {
      purchaseId: props.info.purchaseId,
    };

    dispatch({ body: body });
  }

  function copyToClipboard(event) {
    event.preventDefault();
    window.getSelection().removeAllRanges();
    let range = document.createRange();
    range.selectNode(serialKeyRef.current);
    window.getSelection().addRange(range);
    document.execCommand("Copy");
    window.getSelection().removeAllRanges();
    toast.success("SerialKey copied!");
  }

  return (
    <div className="card border-0 rounded-lg shadow-cm mb-3 col-md-12">
      <div className="card-body px-2">
        <LabelGroup
          label="Purchase ID"
          // eslint-disable-next-line no-undef
          value={props.info.purchaseId.replace(/\B(?=(\d{4})+(?!\d))/g, "-")}
          marginBottom={status === "SUCCESS"}
        />
        {status === "SUCCESS" && (
          <>
            <LabelGroup
              label="ePIN/Serial key"
              value={serialKey}
              marginBottom={true}
              dataRef={serialKeyRef}
            />
            <button
              className="btn btn-sm btn-light mb-2 d-block px-3"
              onClick={copyToClipboard}
            >
              Copy
            </button>
            {!isNaN(expiryDate) && (
              <LabelGroup
                label="Expiry Date"
                value={moment(expiryDate).format("DD-MMM-yyyy")}
              />
            )}
          </>
        )}
        {status === "PROCESSING" && (
          <>
            <div className="small my-3">
              Please wait 30 seconds to 2 minutes to complete processing.
            </div>
            <div className="clearfix">
              <button
                className="btn btn-success float-left"
                onClick={checkStatus}
                disabled={loading || block > 0}
              >
                {loading && (
                  <>
                    <span
                      className="spinner-border spinner-border-sm"
                      role="status"
                      aria-hidden="true"
                    ></span>
                    &nbsp;
                  </>
                )}
                Check Status {block > 0 && `(${block})`}
              </button>
            </div>
          </>
        )}
      </div>
    </div>
  );
}

export default PurchaseEPinInfo;
