import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useApiFetch } from "../../common/DataFetchHook";
import Welcome from "../../common/Welcome";
import { checkMobile, signUp, verifyMobile } from "../AuthenticationAPI";
import RegisterForm from "./RegisterForm";
import { v4 as uuidv4 } from "uuid";
import config from "../../common/config.json";
import TextInput from "../../common/TextInput";
import { toast } from "react-toastify";

function Register(props) {
  const [form, setForm] = useState({
    mobile: "",
    password: "",
    name: "",
    region: "",
    token: "",
    code: "",
    agreement: false,
  });
  const [signUpError, setSignUpError] = useState("");
  const [errors, setErrors] = useState({});
  const [codeError, setCodeError] = useState("");

  const [tkData, tkLoading, tkError, tkDispatch] = useApiFetch((action) =>
    checkMobile(action.body, action.token)
  );
  const [vfData, vfLoading, vfError, vfDispatch] = useApiFetch((action) =>
    verifyMobile(action.body, action.token)
  );
  const [cfData, cfLoading, cfError, cfDispatch] = useApiFetch((action) =>
    signUp(action.body, action.token)
  );

  useEffect(() => {
    document.title = "VStore | Sign Up";
  }, []);

  useEffect(() => {
    if (tkData && !tkData.taken) {
      let body = { mobile: form.mobile };
      vfDispatch({ body: body });
    } else if (tkData && tkData.taken) {
      setSignUpError("Mobile has been taken by another registered account.");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [tkData]);

  useEffect(() => {
    if (vfData && vfData.result !== "SUCCESS") {
      //setSignUpError("Something went wrong. Please try again.");
    } else if (vfData && vfData.result === "SUCCESS") {
      toast.success(
        "Verification code has been sent to your account's registered mobile."
      );
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [vfData]);

  useEffect(() => {
    if (cfData && cfData.result === "SUCCESS") {
      toast.success("Your account has been created successfully.");
      localStorage.setItem(config.KEY_TOKEN, cfData.token);
      localStorage.setItem(config.KEY_USER_ID, cfData.customerId);
      props.history.replace("/dashboard");
    } else if (cfData && cfData.result !== "SUCCESS") {
      setSignUpError("Something went wrong. Please try again.");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [cfData]);

  useEffect(() => {
    if (tkError) {
      setSignUpError(tkError);
    }
  }, [tkError]);

  useEffect(() => {
    if (vfError) {
      setSignUpError(vfError);
    }
  }, [vfError]);

  useEffect(() => {
    if (cfError) {
      setSignUpError(cfError);
    }
  }, [cfError]);

  function handleChange({ target }) {
    const value = target.name === "agreement" ? target.checked : target.value;
    if (target.name === "mobile" && isNaN(value)) {
      return;
    }

    if (target.name === "code" && isNaN(value)) {
      return;
    }

    setForm({
      ...form,
      [target.name]: value,
    });
  }

  function resendCode(event) {
    event.preventDefault();
    if (vfLoading) {
      return;
    }
    setSignUpError("");
    let body = { mobile: form.mobile };
    vfDispatch({ body: body });
  }

  function handleSignUp(event) {
    event.preventDefault();
    setSignUpError("");
    setCodeError("");

    if (!isFormValid()) return;

    let body = { mobile: form.mobile };

    tkDispatch({ body: body });
  }

  function handleVerify(event) {
    event.preventDefault();
    setSignUpError("");
    setCodeError("");

    if (!form.code || form.code.length !== 6) {
      setCodeError("Verification code must be 6 digit letter.");
      return;
    }

    const token = uuidv4();
    localStorage.setItem(config.KEY_DEVICE_ID, token);

    let body = {
      mobile: form.mobile,
      password: form.password,
      name: form.name,
      region: form.region,
      token: token,
      code: form.code,
    };

    cfDispatch({ body: body });
  }

  function isFormValid() {
    const _errors = {};
    const regex = RegExp("^(09)\\d{7,12}$");
    if (!form.mobile || !regex.test(form.mobile)) {
      _errors.mobile = "Mobile number is invalid.";
    }

    if (!form.password || form.password.length < 8) {
      _errors.password = "Password should not be less than 8 characters.";
    }

    if (!form.name) {
      _errors.name = "Please fill your full name.";
    }

    if (!form.region) {
      _errors.region = "Region is required.";
    }

    if (!form.agreement) {
      _errors.agreement = "You must agree terms & conditions.";
    }

    setErrors(_errors);
    return Object.keys(_errors).length === 0;
  }

  let component = (
    <div className="col-md-8 col-lg-5 my-5 mx-auto">
      <div className="card shadow border-0">
        <div className="card-body">
          <h1 className="text-center mb-4 text-primary logo-text">vstore</h1>
          {signUpError && (
            <div className="alert alert-danger mb-3 border-0" role="alert">
              {signUpError}
            </div>
          )}
          <RegisterForm
            data={form}
            errors={errors}
            onChange={handleChange}
            onSubmit={handleSignUp}
            loading={tkLoading || vfLoading}
          />
          <div className="row mt-4">
            <div className="mx-auto small">
              <span>Already have an account?</span>
              <Link to="/login" className="card-link text-primary ml-1">
                Sign In
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  if (tkData && !tkData.taken) {
    component = (
      <div className="col-md-8 col-lg-5 my-5 mx-auto">
        <div className="card shadow border-0">
          <div className="card-body">
            <p className="small">
              Enter the verification code that we have sent to your mobile. You
              need to verify your mobile to complete the registration.
            </p>
            {signUpError && (
              <div className="alert alert-danger mb-3 border-0" role="alert">
                {signUpError}
              </div>
            )}
            <form onSubmit={handleVerify}>
              <TextInput
                label="Code"
                name="code"
                type="text"
                placeholder="6 digits code"
                value={form.code}
                onChange={handleChange}
                error={codeError}
              />
              <button
                type="submit"
                className="btn btn-primary btn-block"
                disabled={cfLoading}
              >
                {cfLoading && (
                  <>
                    <span
                      className="spinner-border spinner-border-sm mr-1"
                      role="status"
                      aria-hidden="true"
                    ></span>
                    &nbsp;
                  </>
                )}
                Verify and sign Up
              </button>
            </form>
            <div className="row mt-3">
              <div className="mx-auto small">
                <a
                  href="!#"
                  className="card-link text-muted font-weight-bold"
                  onClick={resendCode}
                >
                  Resend code ?
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <>
      <div className="my-4 col-lg-7 d-none d-md-none d-lg-block">
        <Welcome />
      </div>
      {component}
    </>
  );
}

export default Register;
