import { useEffect, useState } from "react";
import { toast } from "react-toastify";
import { useApiFetch } from "../common/DataFetchHook";
import TextArea from "../common/TextArea";
import HowToGuide from "./HowToGuide";
import { purchaseEpin } from "./ProductAPI";
import ProductDetailHeader from "./ProductDetailHeader";

function PurchaseEPin(props) {
  const [form, setForm] = useState({
    remark: "",
  });
  const [data, loading, error, dispatch] = useApiFetch((action) =>
    purchaseEpin(action.body, action.token)
  );

  useEffect(() => {
    if (data) {
      props.onSuccess(props.data, {
        expiryDate: data.expiryDate,
        status: data.purchaseStatus,
        serialKey: data.serialKey,
        purchaseId: data.purchaseId,
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  useEffect(() => {
    if (error) {
      toast.error(error);
    }
  }, [error]);

  function handleChange({ target }) {
    setForm({
      ...form,
      [target.name]: target.value,
    });
  }

  function handleSubmit(event) {
    event.preventDefault();
    let body = {
      listingId: props.data.id,
      remark: form.remark,
    };

    dispatch({ body: body });
  }

  return (
    <>
      <ProductDetailHeader
        logo={props.data.photoUrl}
        title={props.data.title}
        amount={props.data.sellingPrice}
      />
      <div className="card border-0 rounded-lg shadow-cm mb-2">
        <form onSubmit={handleSubmit} className="card-body px-4 py-3">
          <TextArea
            label="Remark"
            name="remark"
            type="text"
            onChange={handleChange}
            placeholder=""
            value={form.remark}
          />
          <div className="clearfix">
            <button
              type="submit"
              className="btn btn-success float-right"
              disabled={loading}
            >
              {loading && (
                <>
                  <span
                    className="spinner-border spinner-border-sm"
                    role="status"
                    aria-hidden="true"
                  ></span>
                  &nbsp;
                </>
              )}
              Purchase
            </button>
          </div>
        </form>
      </div>
      {props.data.howToGuideUrl && (
        <HowToGuide url={props.data.howToGuideUrl} />
      )}
    </>
  );
}

export default PurchaseEPin;
