function ErrorMessage(props) {
  if (props.message) {
    return (
      <div className="alert alert-danger" role="alert">
        {props.message}
        {props.dismiss && (
          <button
            type="button"
            className="close"
            onClick={() => props.dismiss()}
          >
            <span aria-hidden="true">&times;</span>
          </button>
        )}
      </div>
    );
  }

  return <></>;
}

export default ErrorMessage;
