import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { v4 as uuidv4 } from "uuid";
import config from "../../common/config.json";
import { useApiFetch } from "../../common/DataFetchHook";
import Welcome from "../../common/Welcome";
import LoginForm from "./LoginForm";
import { login } from "../AuthenticationAPI";

function Login(props) {
  const [form, setForm] = useState({
    mobile: "",
    password: "",
    token: "",
  });
  const [loginError, setLoginError] = useState("");
  const [data, loading, error, dispatch] = useApiFetch((action) =>
    login(action.body, action.token)
  );

  const [errors, setErrors] = useState({});

  useEffect(() => {
    document.title = "VStore | Login";
    if (data && data.result !== "FAIL") {
      localStorage.setItem(config.KEY_TOKEN, data.token);
      localStorage.setItem(config.KEY_USER_ID, data.customerId);
      props.history.replace("/dashboard");
    } else if (data) {
      setLoginError("Invalid login ID or password.");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  useEffect(() => {
    if (error) {
      setLoginError(error);
    }
  }, [error]);

  function handleChange({ target }) {
    const value = target.value;
    if (target.name === "mobile" && isNaN(value)) {
      return;
    }
    setForm({
      ...form,
      [target.name]: value,
    });
  }

  function handleSubmit(event) {
    event.preventDefault();
    setLoginError("");

    if (!isFormValid()) return;

    const token = uuidv4();
    form.token = token;
    localStorage.setItem(config.KEY_DEVICE_ID, token);

    dispatch({ body: form });
  }

  function isFormValid() {
    const _errors = {};
    const regex = RegExp("^(09)\\d{7,12}$");
    if (!form.mobile || !regex.test(form.mobile)) {
      _errors.mobile = "Mobile number is invalid.";
    }

    if (!form.password || form.password.length < 8) {
      _errors.password = "Password should not be less than 8 characters.";
    }
    setErrors(_errors);
    return Object.keys(_errors).length === 0;
  }

  return (
    <>
      <div className="my-4 col-lg-7 d-none d-md-none d-lg-block">
        <Welcome />
      </div>
      <div className="col-md-8 col-lg-5 my-5 mx-auto">
        <div className="card shadow border-0">
          <div className="card-body">
            <h1 className="text-center mb-4 text-primary logo-text">vstore</h1>
            {loginError && (
              <div className="alert alert-danger mb-3 border-0" role="alert">
                {loginError}
              </div>
            )}
            <LoginForm
              data={form}
              onChange={handleChange}
              onSubmit={handleSubmit}
              errors={errors}
              loading={loading}
            />
            <div className="row mt-4">
              <div className="mx-auto small">
                <span>Don't have an account?</span>
                <Link to="/sign-up" className="card-link text-primary ml-1">
                  Sign Up
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Login;
