import { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import appConfig from "../common/config.json";
import { useApiFetch } from "../common/DataFetchHook";
import PasswordInput from "../common/PasswordInput";
import TextInput from "../common/TextInput";
import { clearAppData } from "../common/Utils";
import Profile from "./Profile";
import * as profileApi from "./ProfileAPI";

function UpdateName(props) {
  const [name, setName] = useState(
    localStorage.getItem(appConfig.KEY_USER_NAME)
  );
  const [nameError, setNameError] = useState("");
  const [data, loading, error, dispatch] = useApiFetch((action) =>
    profileApi.changeName(action.body, action.token)
  );

  useEffect(() => {
    if (data && data.result === "SUCCESS") {
      localStorage.setItem(appConfig.KEY_USER_NAME, name);
      props.onUpdated();
      toast.success("Your name has been changed successfully.");
    } else if (data) {
      toast.error("Something went wrong.");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  useEffect(() => {
    if (error) {
      toast.error(error);
    }
  }, [error]);

  function handleChange({ target }) {
    setName(target.value);
  }

  function handleSubmit(event) {
    event.preventDefault();
    setNameError("");

    if (!name) {
      setNameError("Please fill your full name.");
      return;
    }

    let oldName = localStorage.getItem(appConfig.KEY_USER_NAME);
    if (oldName === name) {
      setNameError("Your previous name and current name are the same.");
      return;
    }

    let body = { newName: name };

    dispatch({ body: body });
  }

  return (
    <div className="card border-0 rounded-lg shadow-cm mb-3">
      <div className="card-header font-weight-bold">Change Name</div>
      <div className="card-body">
        <form onSubmit={handleSubmit}>
          <TextInput
            label="Your Name"
            name="name"
            type="text"
            placeholder="Your preferred name"
            error={nameError}
            value={name}
            onChange={handleChange}
          />
          <div className="clearfix">
            <button
              type="submit"
              className="btn btn-success float-right"
              disabled={loading}
            >
              {loading && (
                <>
                  <span
                    className="spinner-border spinner-border-sm mr-1"
                    role="status"
                    aria-hidden="true"
                  ></span>
                  &nbsp;
                </>
              )}
              Change
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

function UpdatePassword(props) {
  const [cred, setCred] = useState({
    oldPassword: "",
    newPassword: "",
  });
  const [errors, setErrors] = useState({});
  const [data, loading, error, dispatch] = useApiFetch((action) =>
    profileApi.changePassword(action.body, action.token)
  );
  const history = useHistory();

  useEffect(() => {
    if (data && data.result === "SUCCESS") {
      clearAppData();
      history.replace("/login");
      toast.success(
        "Your password has been changed successfully. Please log in again now."
      );
    } else if (data) {
      toast.error("Your old password is not correct.");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  useEffect(() => {
    if (error) {
      toast.error(error);
    }
  }, [error]);

  function handleChange({ target }) {
    setCred({
      ...cred,
      [target.name]: target.value,
    });
  }

  function handleSubmit(event) {
    event.preventDefault();

    const _errors = {};
    if (!cred.oldPassword || cred.oldPassword.length < 8) {
      _errors.oldPassword = "Password should not be less than 8 characters.";
    }

    if (!cred.newPassword || cred.newPassword.length < 8) {
      _errors.newPassword = "Password should not be less than 8 characters.";
    }

    setErrors(_errors);
    if (Object.keys(_errors).length > 0) {
      return;
    }

    dispatch({ body: cred });
  }

  return (
    <div className="card border-0 rounded-lg shadow-cm">
      <div className="card-header font-weight-bold">Change Password</div>
      <div className="card-body">
        <form onSubmit={handleSubmit}>
          <PasswordInput
            label="Old Password"
            name="oldPassword"
            type="text"
            placeholder="Your current password"
            value={cred.oldPassword}
            onChange={handleChange}
            error={errors.oldPassword}
          />
          <PasswordInput
            label="New Password"
            name="newPassword"
            type="text"
            placeholder="Minimum 8 characters"
            value={cred.newPassword}
            onChange={handleChange}
            error={errors.newPassword}
          />
          <div className="clearfix">
            <button
              type="submit"
              className="btn btn-success float-right"
              disabled={loading}
            >
              {loading && (
                <>
                  <span
                    className="spinner-border spinner-border-sm mr-1"
                    role="status"
                    aria-hidden="true"
                  ></span>
                  &nbsp;
                </>
              )}
              Change
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

function UpdateProfile(props) {
  const [dummy, setDummy] = useState({});

  useEffect(() => {
    document.title = "VStore | Update Profile";
  }, []);

  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block order-2">
        <Profile tab="setting" dummy={dummy} />
      </div>
      <div className="col-md-12 col-lg-8 order-1">
        <h5>Update Profile</h5>
        <hr />

        <UpdateName onUpdated={() => setDummy({})} />
        <UpdatePassword />
      </div>
    </>
  );
}

export default UpdateProfile;
