import AppBar from "./AppBar";
import Footer from "./Footer";
import appConfig from "./config.json";
import { useEffect } from "react";

function Template(props) {
  const notAuthenticated = !localStorage.getItem(appConfig.KEY_TOKEN);

  useEffect(() => {
    if (notAuthenticated) {
      document.body.classList.replace("bg-custom", "bg-primary");
    } else {
      document.body.classList.replace("bg-primary", "bg-custom");
    }
  }, [notAuthenticated]);

  return (
    <>
      {!notAuthenticated && <AppBar active={props.active} />}
      <main role="main" className="flex-shrink-0">
        <div className="container">
          <div className="row mt-3 mb-5">
            {/* <div className="col-lg-4 d-none d-md-none d-lg-block">
            <Profile />
          </div>
          <div className="col-md-12 col-lg-8">{props.children}</div> */}
            {props.children}
          </div>
        </div>
      </main>
      <Footer />
    </>
  );
}

export default Template;
