import axios from "axios";

export function login(body, cancelToken) {
  const url = process.env.REACT_APP_API_URL + "/public/sign-in";
  return axios.post(url, body, { cancelToken: cancelToken });
}

export function signUp(body, cancelToken) {
  const url = process.env.REACT_APP_API_URL + "/public/sign-up";
  return axios.post(url, body, { cancelToken: cancelToken });
}

export function checkMobile(body, cancelToken) {
  const url = process.env.REACT_APP_API_URL + "/public/check-mobile-taken";
  return axios.post(url, body, { cancelToken: cancelToken });
}

export function verifyMobile(body, cancelToken) {
  const url =
    process.env.REACT_APP_API_URL + "/public/request-mobile-verification";
  return axios.post(url, body, { cancelToken: cancelToken });
}

export function verifyReset(body, cancelToken) {
  const url =
    process.env.REACT_APP_API_URL + "/public/request-to-reset-password";
  return axios.post(url, body, { cancelToken: cancelToken });
}

export function resetPassword(body, cancelToken) {
  const url = process.env.REACT_APP_API_URL + "/public/reset-password";
  return axios.post(url, body, { cancelToken: cancelToken });
}
