import { useRef } from "react";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";
import Profile from "../Profile";
import WavePayLogo from "./ic_wave.png";

function WavePayDeposit(props) {
  const phoneNoRef = useRef(null);

  function copyToClipboard(event) {
    event.preventDefault();
    window.getSelection().removeAllRanges();
    let range = document.createRange();
    range.selectNode(phoneNoRef.current);
    window.getSelection().addRange(range);
    document.execCommand("Copy");
    window.getSelection().removeAllRanges();
    toast.success("Account number copied!");
  }

  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block order-2">
        <Profile />
      </div>
      <div className="col-md-12 col-lg-8 order-1">
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb bg-white shadow-cm">
            <li className="breadcrumb-item">
              <Link to="/deposit-options" className="card-link">
                Deposit VP Point
              </Link>
            </li>
            <li className="breadcrumb-item active">Wave Pay</li>
          </ol>
        </nav>
        <div className="card border-0 rounded-lg shadow-cm col-md-12">
          <div className="card-body px-2">
            <div className="row mb-3">
              <img
                src={WavePayLogo}
                alt=""
                width="68"
                height="68"
                className="mx-auto"
              />
            </div>
            <div className="text-center text-muted mb-3">
              Transfer to WavePay:
            </div>
            <div className="text-center font-weight-medium mb-2">
              Account Number
            </div>
            <div className="text-center text-primary mb-2" ref={phoneNoRef}>
              09777773363
            </div>
            <div className="row mb-4">
              <button
                className="btn btn-sm btn-light mx-auto px-3"
                onClick={copyToClipboard}
              >
                Copy
              </button>
            </div>
            <ol className="bg-light border rounded-lg py-3 pr-3 small mb-0">
              <li className="mb-2">
                Transfer amount to VStore's WavePay account.
              </li>
              <li>
                Send WavePay's transaction ID, your VStore account's mobile
                number and your name (e.g Transaction ID: 182717291, Aung Aung
                (0998765432) to VStore Myanmar Facebook messenger or Viber
                (+959447489633, +959447489644)).
              </li>
            </ol>
          </div>
        </div>
      </div>
    </>
  );
}

export default WavePayDeposit;
