import { useEffect, useState } from "react";
import { getBalanceHistory } from "./HistoryAPI";
import moment from "moment";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import $ from "jquery";
import Loading from "../common/Loading";
import ErrorMessage from "../common/ErrorMessage";
import { useArrayFetch } from "../common/DataFetchHook";
import Profile from "../profile/Profile";

function VPBalanceHistory(props) {
  const fetch = (dateString) => getBalanceHistory(dateString);
  const [date, setDate] = useState(moment().format("DD-MMM-YYYY"));
  const [data, loading, error] = useArrayFetch(date, fetch);

  useEffect(() => {
    document.title = "VStore | VP Balance History";
    let datePicker = $("#datepicker").datepicker({
      uiLibrary: "bootstrap4",
      format: "dd-mmm-yyyy",
      maxDate: new Date(),
      change: handleChange,
    });
    return () => {
      datePicker && datePicker.destroy();
    };
  }, []);

  function handleChange({ target }) {
    setDate(target.value);
  }

  function getIcon(dir) {
    if (dir === "WITHDRAW") {
      return <FontAwesomeIcon icon={["fas", "minus-circle"]} size="lg" />;
    }
    return <FontAwesomeIcon icon={["fas", "plus-circle"]} size="lg" />;
  }

  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block order-2">
        <Profile />
      </div>
      <div className="col-md-12 col-lg-8 order-1">
        <div className="row">
          <div className="col-md-12">
            <h5>VP Balance History</h5>
            <hr />
            <form className="form-row mb-3">
              <div className="col-auto">
                <input
                  id="datepicker"
                  type="text"
                  className="form-control bg-white"
                  defaultValue={date}
                  readOnly
                />
              </div>
            </form>

            <Loading show={loading} />
            <ErrorMessage message={error} />

            {!loading && !error && data && !data.length && (
              <div className="text-center text-muted">No data.</div>
            )}

            {!loading &&
              !error &&
              data &&
              data.map((h, index) => {
                let color =
                  h.entryType === "WITHDRAW" ? " text-danger" : " text-success";
                let statusColor = " text-muted";
                switch (h.status) {
                  case "PROCESSING":
                    statusColor = " text-warning";
                    break;
                  case "SUCCESS":
                    statusColor = " text-success";
                    break;
                  case "FAIL":
                    statusColor = " text-danger";
                    break;
                  default:
                    break;
                }
                return (
                  <div
                    key={index}
                    className="card border-0 rounded-lg shadow-cm mb-3 col-md-12"
                  >
                    <div className="card-body px-0">
                      <div className="media">
                        <span className={"mr-3" + color}>
                          {getIcon(h.entryType)}
                        </span>
                        <div className="media-body">
                          <div className="d-flex justify-content-between">
                            <label
                              className={"my-0 font-weight-medium" + color}
                            >
                              VP{" "}
                              {h.amount.toLocaleString("en", {
                                minimumFractionDigits: 2,
                              })}
                            </label>
                            <label
                              className={
                                "my-0 font-weight-medium" + statusColor
                              }
                            >
                              {h.status}
                            </label>
                          </div>
                          <div className="mb-2 small">
                            {moment(h.txnDate).format("DD-MMM-yyyy hh:mm:ss A")}
                          </div>
                          <p className="text-muted mb-2">{h.description}</p>
                          <div className="row">
                            <div className="col-6">
                              <label className="font-weight-medium mb-0">
                                P/B (VP)
                              </label>
                            </div>
                            <div className="col-6">
                              <label className="font-weight-medium mb-0">
                                N/B (VP)
                              </label>
                            </div>
                            <div className="col-6">
                              <span className="small">
                                {h.balanceBefore.toLocaleString("en", {
                                  minimumFractionDigits: 2,
                                })}
                              </span>
                            </div>
                            <div className="col-6">
                              <span className={"small" + color}>
                                {h.balanceAfter.toLocaleString("en", {
                                  minimumFractionDigits: 2,
                                })}
                              </span>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                );
              })}
          </div>
        </div>
      </div>
    </>
  );
}

export default VPBalanceHistory;
