import { useEffect } from "react";
import { useDataFetch } from "../../common/DataFetchHook";
import ErrorMessage from "../../common/ErrorMessage";
import Loading from "../../common/Loading";
import PurchaseHeader from "../../purchase/PurchaseHeader";
import PurchaseStatus from "../../purchase/PurchaseStatus";
import { getEpinPurchase } from "../HistoryAPI";
import { Link, useLocation } from "react-router-dom";
import Profile from "../../profile/Profile";
import PurchaseEPinInfo from "../../purchase/PurchaseEPinInfo";

function PurchaseEPinDetail(props) {
  const fetch = (id) => getEpinPurchase(id);
  const qDate = props.match.params.date;
  const [data, loading, error] = useDataFetch(
    props.match.params.purchaseId,
    fetch
  );

  const params = new URLSearchParams(useLocation().search);

  useEffect(() => {
    document.title = "VStore | ePIN Purchase Detail";
  }, [props.match.params.purchaseId]);

  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block order-2">
        <Profile tab="history" />
      </div>
      <div className="col-md-12 col-lg-8 order-1">
        <div className="row">
          <div className="col-md-12">
            <nav aria-label="breadcrumb">
              <ol className="breadcrumb bg-white shadow-cm">
                <li className="breadcrumb-item">
                  <Link to={"/epin-history/" + qDate} className="card-link">
                    E-Pin History
                  </Link>
                </li>
                <li className="breadcrumb-item active">
                  E-Pin Purchase Detail
                </li>
              </ol>
            </nav>
          </div>
        </div>
        <div className="row">
          <div className="col-md-12">
            <Loading show={loading} />
            <ErrorMessage message={error} />

            {!loading && !error && data && (
              <>
                <PurchaseHeader
                  logo={params.get("logo")}
                  title={data.productTitle}
                  amount={data.paidAmount}
                />
                <PurchaseStatus status={data.status} />
                <PurchaseEPinInfo info={data} />
              </>
            )}
          </div>
        </div>
      </div>
    </>
  );
}

export default PurchaseEPinDetail;
