import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";
import { useApiFetch } from "../../common/DataFetchHook";
import PasswordInput from "../../common/PasswordInput";
import TextInput from "../../common/TextInput";
import Welcome from "../../common/Welcome";
import { resetPassword, verifyReset } from "../AuthenticationAPI";

function ResetPassword(props) {
  const [form, setForm] = useState({
    mobile: "",
    newPassword: "",
    code: "",
  });
  const [errors, setErrors] = useState({});
  const [resetError, setResetError] = useState("");

  const [vfData, vfLoading, vfError, vfDispatch] = useApiFetch((action) =>
    verifyReset(action.body, action.token)
  );

  const [rsData, rsLoading, rsError, rsDispatch] = useApiFetch((action) =>
    verifyReset(action.body, action.token)
  );

  const [data, loading, error, dispatch] = useApiFetch((action) =>
    resetPassword(action.body, action.token)
  );

  useEffect(() => {
    document.title = "VStore | Reset Password";
  }, []);

  useEffect(() => {
    if (vfData && vfData.result !== "SUCCESS") {
      setResetError("Account does not exist for this mobile.");
    } else if (vfData && vfData.result === "SUCCESS") {
      toast.success(
        "Verification code has been sent to your account's registered mobile."
      );
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [vfData]);

  useEffect(() => {
    if (rsData && rsData.result !== "SUCCESS") {
      //setResetError("Account does not exist for this mobile.");
    } else if (rsData && rsData.result === "SUCCESS") {
      toast.success(
        "Verification code has been sent to your account's registered mobile."
      );
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [vfData]);

  useEffect(() => {
    if (data && data.result !== "SUCCESS") {
      setResetError(
        "Problem occurred at resetting your password. Please try again."
      );
    } else if (data && data.result === "SUCCESS") {
      toast.success(
        "Your password has been changed to your new password. You can now log in."
      );
      props.history.replace("/login");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  useEffect(() => {
    if (error) {
      setResetError(error);
    }
  }, [error]);

  useEffect(() => {
    if (vfError) {
      setResetError(vfError);
    }
  }, [vfError]);

  useEffect(() => {
    if (rsError) {
      toast.error(rsError);
    }
  }, [rsError]);

  function handleChange({ target }) {
    const value = target.value;
    if (target.name === "mobile" && isNaN(value)) {
      return;
    }

    if (target.name === "code" && isNaN(value)) {
      return;
    }

    setForm({
      ...form,
      [target.name]: value,
    });
  }

  function handleVerifyReset(event) {
    event.preventDefault();
    setResetError("");
    const _errors = {};
    const regex = RegExp("^(09)\\d{7,12}$");
    if (!form.mobile || !regex.test(form.mobile)) {
      _errors.mobile = "Mobile number is invalid.";
    }
    setErrors(_errors);

    if (Object.keys(_errors).length > 0) {
      return;
    }

    let body = { mobile: form.mobile };

    vfDispatch({ body: body });
  }

  function handleReset(event) {
    event.preventDefault();
    setResetError("");
    const _errors = {};
    if (!form.newPassword || form.newPassword.length < 8) {
      _errors.newPassword = "Password should not be less than 8 characters.";
    }

    if (!form.code || form.code.length !== 6) {
      _errors.code = "Verification code must be 6 digit letter.";
    }

    setErrors(_errors);

    if (Object.keys(_errors).length > 0) {
      return;
    }

    dispatch({ body: form });
  }

  function resendCode(event) {
    event.preventDefault();
    if (rsLoading) {
      return;
    }
    setResetError("");
    let body = { mobile: form.mobile };
    rsDispatch({ body: body });
  }

  let component = (
    <div className="col-md-8 col-lg-5 my-5 mx-auto">
      <div className="card shadow border-0">
        <div className="card-body">
          <h1 className="text-center mb-4 text-primary logo-text">vstore</h1>
          <form onSubmit={handleVerifyReset}>
            <div className="form-group">
              <div className="bg-light rounded px-3 py-2 border">
                <span className="small">
                  Verification code will be sent to your registered mobile of
                  your account.
                </span>
              </div>
            </div>
            {resetError && (
              <div className="alert alert-danger mb-3 border-0" role="alert">
                {resetError}
              </div>
            )}
            <TextInput
              label="Mobile"
              name="mobile"
              type="text"
              placeholder="Your registered mobile"
              value={form.mobile}
              onChange={handleChange}
              error={errors.mobile}
            />
            <button type="submit" className="btn btn-primary btn-block">
              {vfLoading && (
                <>
                  <span
                    className="spinner-border spinner-border-sm mr-1"
                    role="status"
                    aria-hidden="true"
                  ></span>
                  &nbsp;
                </>
              )}
              Reset Password
            </button>
          </form>
          <div className="row mt-3">
            <div className="mx-auto small">
              <Link to="/login" className="card-link text-primary">
                Back to sign in
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  if (vfData && vfData.result === "SUCCESS") {
    component = (
      <div className="col-md-8 col-lg-5 my-5 mx-auto">
        <div className="card shadow border-0">
          <div className="card-body">
            <p className="small">
              Enter the verification code that we have sent to your mobile. You
              need to verify your mobile to complete the registration.
            </p>
            {resetError && (
              <div className="alert alert-danger mb-3 border-0" role="alert">
                {resetError}
              </div>
            )}
            <form onSubmit={handleReset}>
              <PasswordInput
                label="New Password"
                name="newPassword"
                type="text"
                placeholder="Minimum 8 characters"
                value={form.newPassword}
                onChange={handleChange}
                error={errors.newPassword}
              />
              <TextInput
                label="Code"
                name="code"
                type="text"
                placeholder="6 digits code"
                value={form.code}
                onChange={handleChange}
                error={errors.code}
              />
              <button type="submit" className="btn btn-primary btn-block">
                {loading && (
                  <>
                    <span
                      className="spinner-border spinner-border-sm mr-1"
                      role="status"
                      aria-hidden="true"
                    ></span>
                    &nbsp;
                  </>
                )}
                Reset Password
              </button>
            </form>
            <div className="row mt-3">
              <div className="mx-auto small">
                <a
                  href="!#"
                  className="card-link text-muted font-weight-bold"
                  onClick={resendCode}
                >
                  Resend code ?
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <>
      <div className="my-4 col-lg-7 d-none d-md-none d-lg-block">
        <Welcome />
      </div>
      {component}
    </>
  );
}

export default ResetPassword;
