import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import NotFoundLogo from "../common/error-404-monochrome.svg";

function NotFound() {
  useEffect(() => {
    let appbar = document.getElementById("appbar");
    let footer = document.getElementById("footer");
    let root = document.getElementById("root");

    root && root.classList.add("bg-custom");
    footer && footer.classList.add("d-none");
    appbar && appbar.classList.add("d-none");

    return () => {
      root && root.classList.remove("bg-custom");
      footer && footer.classList.remove("d-none");
      appbar && appbar.classList.remove("d-none");
    };
  }, []);

  return (
    <>
      <div className="container py-4">
        <div className="row justify-content-center">
          <div className="col-lg-6">
            <div className="text-center mt-4">
              <img
                className="mb-4 img-error"
                width="250"
                src={NotFoundLogo}
                alt="Not found"
              />
              <p className="lead">
                This requested URL was not found on this server.
              </p>
              <Link to="/" className="card-link">
                <FontAwesomeIcon
                  icon={["fas", "arrow-left"]}
                  className="mr-1"
                />{" "}
                Return to Home
              </Link>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default NotFound;
