import PurchaseEPinInfo from "../purchase/PurchaseEPinInfo";
import PurchaseHeader from "../purchase/PurchaseHeader";
import PurchaseStatus from "../purchase/PurchaseStatus";
import PurchaseTopupInfo from "../purchase/PurchaseTopupInfo";

function PurchaseResult(props) {
  let info = <></>;

  switch (props.data.productType) {
    case "EPIN":
      info = <PurchaseEPinInfo info={props.result} />;
      break;
    case "TELECOM_TOPUP":
      info = <PurchaseTopupInfo info={props.result} />;
      break;
    default:
      break;
  }

  return (
    <>
      <PurchaseHeader
        logo={props.data.photoUrl}
        title={props.data.title}
        amount={props.data.sellingPrice}
      />
      <PurchaseStatus status={props.result.status} />

      {info}
    </>
  );
}

export default PurchaseResult;
