import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { toast } from "react-toastify";

function ProductCell(props) {
  function handleClick(event) {
    event.preventDefault();

    if (props.status === "INACTIVE") {
      toast.error("Product/service is not ready for sale.");
    } else {
      props.onClick();
    }
  }

  return (
    <a href="!#" className="text-decoration-none" onClick={handleClick}>
      <div className="card border-0 rounded-lg shadow-cm list-group-item-action h-100">
        <div className="card-body">
          {props.status === "INACTIVE" && (
            <FontAwesomeIcon
              icon={["fas", "exclamation-circle"]}
              size="sm"
              className="text-danger position-absolute"
              style={{ right: "16px" }}
            />
          )}
          <div className="row">
            <div className="mx-auto">
              <img
                src={props.logo}
                height="50"
                alt="..."
                style={{
                  maxWidth: "100px",
                }}
              />
            </div>
          </div>

          <div
            className="text-secondary text-center user-select-none mt-3"
            style={{
              maxWidth: "100%",
              wordWrap: "break-word",
            }}
          >
            {props.title}
          </div>

          <h5
            className="text-success font-weight-medium text-center user-select-none mt-2 mb-0"
            style={{
              maxWidth: "100%",
              wordWrap: "break-word",
            }}
          >
            {props.price} VP
          </h5>
        </div>
      </div>
    </a>
  );
}

export default ProductCell;
