import axios from "axios";

export function getNotifications(cancelToken) {
  const url = process.env.REACT_APP_API_URL + "/private/get-notifications";
  return axios.get(url, { cancelToken: cancelToken });
}

export function markAsRead(body, cancelToken) {
  const url = process.env.REACT_APP_API_URL + "/private/mark-notif-as-read";
  return axios.post(url, body, { cancelToken: cancelToken });
}

export function deleteNotifications(cancelToken) {
  const url = process.env.REACT_APP_API_URL + "/private/delete-notifications";
  return axios.get(url, { cancelToken: cancelToken });
}
