import React, { useEffect, useState } from "react";
import { library } from "@fortawesome/fontawesome-svg-core";
import { fab } from "@fortawesome/free-brands-svg-icons";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { far } from "@fortawesome/free-regular-svg-icons";
import Login from "./authentication/login/Login";
import { Redirect, Route, Switch, useHistory } from "react-router-dom";
import PrivateRoute from "./common/PrivateRoute";
import NotFound from "./common/NotFound";
import Register from "./authentication/register/Register";
import ResetPassword from "./authentication/reset/ResetPassword";
import AuthRoute from "./common/AuthRoute";
import axios from "axios";
import appConfig from "./common/config.json";
import { ToastContainer } from "react-toastify";
import Catalogs from "./service/catalog/Catalogs";
import Products from "./service/product/Products";
import Notification from "./notification/Notification";
import VPTransferHistory from "./history/VPTransferHistory";
import VPBalanceHistory from "./history/VPBalanceHistory";
import PurchaseEPinHistory from "./history/PurchaseEPinHistory";
import PurchaseTopupHistory from "./history/PurchaseTopupHistory";
import PurchaseEPinDetail from "./history/detail/PurchaseEPinDetail";
import PurchaseTopupDetail from "./history/detail/PurchaseTopupDetail";
import Product from "./service/Product";
import Template from "./common/Template";
import MpuPayment from "./profile/wallet/MpuPayment";
import WavePayDeposit from "./profile/wallet/WavePayDeposit";
import DepositOptions from "./profile/wallet/DepositOptions";
import BankTransfer from "./profile/wallet/BankTransfer";
import TransferPoint from "./profile/wallet/TransferPoint";
import UseCouponCode from "./profile/UseCouponCode";
import RedeemVoucherCode from "./profile/RedeemVoucherCode";
import UpdateProfile from "./profile/UpdateProfile";
import Dashboard from "./profile/Dashboard";

library.add(fab, fas, far);

var JSONbig = require("json-bigint")({ storeAsString: true });

axios.defaults.transformResponse = (data) => JSONbig.parse(data);
axios.interceptors.request.use(
  function (config) {
    const headers = {};

    const token = localStorage.getItem(appConfig.KEY_TOKEN);
    const userId = localStorage.getItem(appConfig.KEY_USER_ID);

    if (token && userId) {
      headers["Authorization"] = token;
      headers["X-USER-ID"] = userId;
    }

    headers["Content-Type"] = "application/json";

    config.headers = headers;
    return config;
  },
  function (error) {
    return Promise.reject(error);
  }
);

axios.interceptors.response.use(
  function (response) {
    console.log(response);
    return response;
  },
  function (error) {
    return Promise.reject(error);
  }
);

function App() {
  const [, setState] = useState({});
  const history = useHistory();

  useEffect(() => {
    let unlisten = history.listen((location, action) => {
      //console.log(location);
      //console.log(action);
      if (action !== "REPLACE") return;

      if (["/login", "/dashboard"].includes(location.pathname)) {
        setState({});
      }
    });
    return () => {
      unlisten();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Template>
      <ToastContainer autoClose={5000} hideProgressBar />
      <Switch>
        <PrivateRoute path="/app/:store/:catalog" component={Products} />
        <PrivateRoute path="/app/:store" component={Catalogs} />
        <PrivateRoute path="/services" exact component={Product} />
        <PrivateRoute path="/notifications" exact component={Notification} />
        <PrivateRoute
          path="/deposit-options"
          exact
          component={DepositOptions}
        />
        <PrivateRoute path="/mpu-payment" exact component={MpuPayment} />
        <PrivateRoute path="/wave-pay" exact component={WavePayDeposit} />
        <PrivateRoute path="/bank-transfer" exact component={BankTransfer} />
        <PrivateRoute path="/dashboard" exact component={Dashboard} />
        <PrivateRoute path="/transfer-point" exact component={TransferPoint} />
        <PrivateRoute path="/use-coupon-code" exact component={UseCouponCode} />
        <PrivateRoute path="/update-profile" exact component={UpdateProfile} />
        <PrivateRoute
          path="/redeem-voucher-code"
          exact
          component={RedeemVoucherCode}
        />
        <PrivateRoute
          path="/transfer-history"
          exact
          component={VPTransferHistory}
        />
        <PrivateRoute
          path="/balance-history"
          exact
          component={VPBalanceHistory}
        />
        <PrivateRoute
          path="/epin-history/:date"
          exact
          component={PurchaseEPinHistory}
        />
        <PrivateRoute
          path="/epin-history"
          exact
          component={PurchaseEPinHistory}
        />
        <PrivateRoute
          path="/topup-history/:date"
          exact
          component={PurchaseTopupHistory}
        />
        <PrivateRoute
          path="/topup-history"
          exact
          component={PurchaseTopupHistory}
        />
        <PrivateRoute
          path="/epin-history/detail/:purchaseId/:date"
          exact
          component={PurchaseEPinDetail}
        />
        <PrivateRoute
          path="/topup-history/detail/:purchaseId/:date"
          exact
          component={PurchaseTopupDetail}
        />
        <AuthRoute path="/login" component={Login} />
        <AuthRoute path="/sign-up" component={Register} />
        <AuthRoute path="/reset-password" component={ResetPassword} />
        <Redirect from="/" to="/dashboard" exact />
        <Route component={NotFound} />
      </Switch>
    </Template>
  );
}

export default App;
