import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useRef } from "react";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";
import Profile from "../Profile";
import AyaBankLogo from "./ic_ayabank.png";
import KbzBankLogo from "./ic_kbz.png";

function BankAccount(props) {
  const accountNumberRef = useRef(null);

  function copyToClipboard(event) {
    event.preventDefault();
    window.getSelection().removeAllRanges();
    let range = document.createRange();
    range.selectNode(accountNumberRef.current);
    window.getSelection().addRange(range);
    document.execCommand("Copy");
    window.getSelection().removeAllRanges();
    toast.success("Account number copied!");
  }

  return (
    <div className="card border-0 rounded-lg shadow-cm mb-3 col-md-12">
      <div className="card-body px-0">
        <div className="media">
          <img src={props.brand} alt="" className="mr-3" width="68" />
          <div className="media-body">
            <div className="row">
              <div className="col">
                <div className="font-weight-medium small">Account Number</div>
                <div className="mb-3 small text-primary" ref={accountNumberRef}>
                  {props.accountNumber}
                </div>
                <div className="font-weight-medium small">Account Name</div>
                <div className="small text-primary">VSTORE MYANMAR LIMITED</div>
              </div>
              <div className="col-auto">
                <button
                  className="btn btn-sm btn-light text-secondary"
                  onClick={copyToClipboard}
                >
                  <FontAwesomeIcon icon={["far", "copy"]} />
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

function BankTransfer(props) {
  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block order-2">
        <Profile />
      </div>
      <div className="col-md-12 col-lg-8 order-1">
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb bg-white shadow-cm">
            <li className="breadcrumb-item">
              <Link to="/deposit-options" className="card-link">
                Deposit VP Point
              </Link>
            </li>
            <li className="breadcrumb-item active">Bank Transfer</li>
          </ol>
        </nav>
        <BankAccount brand={AyaBankLogo} accountNumber="0054 1020 1001 1584" />
        <BankAccount brand={KbzBankLogo} accountNumber="2621 0326 2006 49901" />

        <div className="card border-0 rounded-lg shadow-cm col-md-12">
          <div className="card-body px-2">
            <ol className="bg-light border rounded-lg py-3 pr-3 small mb-0">
              <li className="mb-2">
                Deposit at bank counter or transfer using your Mobile Banking.
              </li>
              <li>
                Send bank slip or screenshot to VStore Myanmar Viber
                (+959447489633, +959447489644) or VStore Myanmar Facebook
                Messenger together with your VStore account into e.g Aung Aung
                (0998765432).
              </li>
            </ol>
          </div>
        </div>
      </div>
    </>
  );
}

export default BankTransfer;
