import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useEffect, useState } from "react";
import { toast } from "react-toastify";
import ConfirmModal from "../common/ConfirmModal";
import { useApiFetch } from "../common/DataFetchHook";
import ErrorMessage from "../common/ErrorMessage";
import InfoModal from "../common/InfoModal";
import Loading from "../common/Loading";
import Profile from "../profile/Profile";
import {
  deleteNotifications,
  getNotifications,
  markAsRead,
} from "./NotificationAPI";

function Notification(props) {
  const [list, setList] = useState([]);
  const [pageError, setPageError] = useState("");
  const [data, loading, error, dispatch] = useApiFetch((action) =>
    getNotifications(action.token)
  );
  const [delData, delLoading, delError, delDispatch] = useApiFetch((action) =>
    deleteNotifications(action.token)
  );
  const [, , rdError, rdDispatch] = useApiFetch((action) =>
    markAsRead(action.body, action.token)
  );

  useEffect(() => {
    document.title = "VStore | Notifications";
    if (!data) {
      loadNoti();
    } else {
      setList(data);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  useEffect(() => {
    if (delData && delData.result === "SUCCESS") {
      loadNoti();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [delData]);

  useEffect(() => {
    if (error) {
      setPageError(error);
    }
  }, [error]);

  useEffect(() => {
    if (delError) {
      setPageError(delError);
    }
  }, [delError]);

  useEffect(() => {
    if (rdError) {
      toast.error(rdError);
    }
  }, [rdError]);

  function deleteAll() {
    delDispatch({});
  }

  function showNoti(noti) {
    let body = { notificationId: noti.notificationId };

    if (noti.status !== "READ") {
      let _list = list.map((n) => {
        if (n.notificationId === noti.notificationId) {
          n.status = "READ";
        }
        return n;
      });

      setList(_list);
      rdDispatch({ body: body });
    }
  }

  function loadNoti() {
    setPageError("");
    dispatch({});
  }

  function formatDate(millis) {
    let date = new Date(millis);
    let formatter = Intl.DateTimeFormat("en-US", {
      dateStyle: "medium",
      timeStyle: "short",
    });
    return formatter.format(date);
  }

  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block order-2">
        <Profile />
      </div>
      <div className="col-md-12 col-lg-8 order-1">
        <div className="row">
          <div className="col-md-12">
            <div className="d-flex justify-content-between">
              <span className="h5 my-auto">Notifications</span>
              {list && list.length > 0 && (
                <button
                  className="btn btn-sm btn-outline-danger"
                  disabled={delLoading}
                  data-toggle="modal"
                  data-target="#confirmModal"
                >
                  {delLoading && (
                    <>
                      <span
                        className="spinner-border spinner-border-sm text-danger mr-1"
                        role="status"
                        aria-hidden="true"
                      ></span>
                      &nbsp;
                    </>
                  )}
                  Delete All
                </button>
              )}
              {list && !list.length && (
                <button
                  className="btn btn-sm btn-outline-primary"
                  onClick={(evt) => loadNoti()}
                  disabled={loading}
                >
                  Refresh
                </button>
              )}
            </div>
            <hr />
            <Loading show={loading} />
            <ErrorMessage message={pageError} />

            {list && !list.length && !loading && (
              <div className="text-center text-muted">
                No notification found.
              </div>
            )}

            {list &&
              list.map((n) => {
                let read =
                  n.status === "READ" ? " text-muted" : " font-weight-bold";
                return (
                  <a
                    href="!#"
                    className="text-decoration-none"
                    key={n.notificationId}
                    data-toggle="modal"
                    data-target={"#detail" + n.notificationId}
                    onClick={(evt) => showNoti(n)}
                  >
                    <div className="card border-0 rounded-lg shadow-cm mb-3 col-md-12 list-group-item-action">
                      <div className="card-body px-0">
                        <div className="media">
                          <div
                            className="mr-3 bg-primary rounded-circle text-white"
                            style={{
                              width: "46px",
                              height: "46px",
                            }}
                          >
                            {n.status !== "READ" && (
                              <div
                                className="rounded-circle"
                                style={{
                                  position: "absolute",
                                  background: "red",
                                  left: "48px",
                                  width: "10px",
                                  height: "10px",
                                }}
                              ></div>
                            )}
                            <div className="d-flex align-items-center justify-content-center h-100">
                              <FontAwesomeIcon
                                icon={["fas", "envelope"]}
                                size="lg"
                              />
                            </div>
                          </div>
                          <div className="media-body">
                            <label className={"my-0 font-weight-medium"}>
                              {n.title}
                            </label>
                            <div className={"mb-2 small"}>
                              {formatDate(n.notificationDate)}
                            </div>
                            <p className={"card-text mb-0"}>{n.body}</p>
                          </div>
                        </div>
                      </div>
                    </div>
                    <InfoModal
                      id={"detail" + n.notificationId}
                      title={n.title}
                      body={n.body}
                    />
                  </a>
                );
              })}
          </div>
        </div>

        <ConfirmModal
          onConfirm={deleteAll}
          body="Are you sure you want to delete all notifications ?"
        />
      </div>
    </>
  );
}

export default Notification;
