import { useEffect, useState } from "react";
import { toast } from "react-toastify";
import { useApiFetch } from "../../common/DataFetchHook";
import TextArea from "../../common/TextArea";
import TextInput from "../../common/TextInput";
import Profile from "../Profile";
import { transferBalance } from "../ProfileAPI";

function TransferPoint(props) {
  const [form, setForm] = useState({
    receiver: "",
    amount: "",
    notes: "",
  });
  const [errors, setErrors] = useState({});
  const [data, loading, error, dispatch] = useApiFetch((action) =>
    transferBalance(action.body, action.token)
  );

  useEffect(() => {
    document.title = "VStore | Transfer VP Point";
    if (data && data.result === "SUCCESS") {
      let msg = `You have successfully transferred VP 
          (${parseFloat(data.amount).toLocaleString("en")} to 
          [${data.receiver}])`;
      toast.success(msg);
    }
  }, [data]);

  useEffect(() => {
    if (error) {
      toast.error(error);
    }
  }, [error]);

  function handleChange({ target }) {
    if (target.name !== "notes" && isNaN(target.value)) {
      return;
    }

    setForm({
      ...form,
      [target.name]: target.value,
    });
  }

  function transfer(event) {
    event.preventDefault();

    setErrors({});
    if (!isFormValid()) {
      return;
    }

    dispatch({ body: form });
  }

  function isFormValid() {
    const _errors = {};
    const regex = RegExp("^(09)\\d{7,12}$");
    if (!form.receiver || !regex.test(form.receiver)) {
      _errors.receiver = "Mobile number is invalid.";
    }
    if (!form.amount || parseFloat(form.amount) < 1000) {
      _errors.amount = "Amount is required. Minimum amount is 1,000 VP";
    }
    setErrors(_errors);
    return Object.keys(_errors).length === 0;
  }

  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block order-2">
        <Profile />
      </div>
      <div className="col-md-12 col-lg-8 order-1">
        <h5>Transfer VP</h5>
        <hr />
        <div className="card border-0 rounded-lg shadow-cm">
          <div className="card-body">
            <p className="small mb-2">
              To tranfer VP points to other VStore users, enter the VStore
              mobile of the VP receiver. The VP receiver must be your Referrer
              or your Referred Friend. Minimum amount to transfer is 1,000 VP.
            </p>
            <form onSubmit={transfer}>
              <TextInput
                label="Transfer To"
                name="receiver"
                type="tel"
                placeholder="VStore mobile number of receiver"
                value={form.receiver}
                onChange={handleChange}
                error={errors.receiver}
              />
              <TextInput
                label="Amount"
                name="amount"
                type="number"
                placeholder="Minimum 1,000 VP"
                value={form.amount}
                onChange={handleChange}
                error={errors.amount}
              />
              <TextArea
                label="Notes"
                name="notes"
                placeholder=""
                value={form.notes}
                onChange={handleChange}
              />
              <p className="bg-light rounded px-3 py-2 border small mb-3">
                Before you transfer, check the receiver's VStore mobile is
                correct or not. VStore will not be responsible for any issues
                occured due to negligence of user.
              </p>
              <div className="clearfix">
                <button
                  type="submit"
                  className="btn btn-success float-right"
                  disabled={loading}
                >
                  {loading && (
                    <>
                      <span
                        className="spinner-border spinner-border-sm"
                        role="status"
                        aria-hidden="true"
                      ></span>
                      &nbsp;
                    </>
                  )}
                  Transfer
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}

export default TransferPoint;
